<template>
  <ion-page class="ion-page">
    <ion-content class="content">
      <img
        src="../images/Beep-Logo.svg"
        class="logo">
      <h1>
        Check if you've<br>
        been hacked
      </h1>
      <div class="buttons">
        <div
          class="btn-holder"
          @click="goToAcc">
          <div class="square-btn">
            <div class="square-btn-content">
              <div class="square-btn-icon"><img src="../images/Icon-Account.svg"></div>
              <div class="square-btn-text">Account</div>
            </div>
            <div class="square-btn-shadow"/>
          </div>
        </div>
        <div
          class="btn-holder"
          @click="goToPwd">
          <div class="square-btn">
            <div class="square-btn-content">
              <div class="square-btn-icon"><img src="../images/Icon-Password.svg"></div>
              <div class="square-btn-text">Password</div>
            </div>
            <div class="square-btn-shadow"/>
          </div>
        </div>
      </div>
      <how-does-it-work/>
    </ion-content>
  </ion-page>
</template>

<script>
import HowDoesItWorkModal from './HowDoesItWorkModal.vue'

export default {
  name: 'Home',
  components: {
    'how-does-it-work': HowDoesItWorkModal,
  },
  methods: {
    goToAcc() {
      this.$router.push('/acc')
    },
    goToPwd() {
      this.$router.push('/pwd')
    },
  },
}
</script>

<style>
@import '../styles/common-styles.css';
</style>

<style scoped>
ion-page {
  text-align: center;
  --ion-text-color: var(--beep-secondary);
}

.logo {
  margin-top: 5vh;
  display: inline-block;
}

h1 {
  height: 14vh;
  margin-top: 15vh;
  color: var(--beep-dark);
  font-size: 32px;
  font-weight: bolder;
  letter-spacing: -1px;
}

.buttons {
  position: relative;
  height: 19vh;
  margin-top: 7vh;
  margin-left: 8%;
  width: 84%;
  clear: both;
}

.btn-holder {
  position: relative;
  float: left;
  width: 45%;
}

.btn-holder + .btn-holder {
  margin-left: 10%;
}

.square-btn {
  position: relative;
  width: 100%;
  border-radius: 5px;
  background-color: var(--beep-light);
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.1);
}

.square-btn:after {
  content: '';
  display: block;
  padding-bottom: 100%;
}

.square-btn-shadow {
  position: absolute;
  height: 80%;
  width: 80%;
  z-index: -1;
  top: 25%;
  left: 10%;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.15);
  -webkit-filter: blur(10px);
  -moz-filter: blur(10px);
  -o-filter: blur(10px);
  -ms-filter: blur(10px);
  filter: blur(10px);
}

.square-btn-content {
  position: absolute;
  width: 100%;
  height: 100%;
}

.square-btn-icon {
  margin-bottom: 10%;
  margin-top: 15%;
  width: 100%;
  height: 35%;
}

.square-btn-icon img {
  height: 100%;
}

.square-btn-text {
  width: 100%;
  color: var(--beep-dark);
  font-size: 15px;
  font-weight: 300;
  letter-spacing: -0.48px;
  line-height: 22px;
}

@media screen and (min-height: 650px) {
  h1 {
    font-size: 40px;
  }
}
</style>



